import { NextRequest, NextResponse } from 'next/server';
import { auth } from '@clerk/nextjs/server';
import { getGamificationStats, BADGES } from '@/services/gamificationService';
import { errors, handleApiError } from '@/utils/error';
import { logApiRequest, logApiResponse } from '@/utils/logger';

export const dynamic = 'force-dynamic';

/**
 * Get user's gamification stats
 * GET /api/gamification/stats
 */
export async function GET(req: NextRequest): Promise<NextResponse> {
  const startTime = Date.now();

  try {
    // 1. Authenticate
    const { userId } = await auth();
    if (!userId) {
      throw errors.unauthorized();
    }

    logApiRequest('GET', '/api/gamification/stats', userId);

    // 2. Get stats
    const stats = await getGamificationStats(userId);

    // 3. Map badge IDs to badge details
    const badgeDetails = stats.badges.map((badgeId) => {
      const badge = Object.values(BADGES).find((b) => b.id === badgeId);
      return badge || { id: badgeId, name: 'Unknown', description: '', icon: 'üèÜ', requirement: 0 };
    });

    // Log response
    const duration = Date.now() - startTime;
    logApiResponse('GET', '/api/gamification/stats', 200, duration, userId);

    return NextResponse.json({
      xp: stats.xp,
      level: stats.level,
      xpForNextLevel: stats.xpForNextLevel,
      badges: badgeDetails,
      currentStreak: stats.currentStreak,
      longestStreak: stats.longestStreak,
    });
  } catch (error) {
    const duration = Date.now() - startTime;
    logApiResponse('GET', '/api/gamification/stats', 500, duration);
    return handleApiError(error);
  }
}
